import React from 'react';
import classnames from 'classnames';
import { TAppItem } from '@/routes/type';
import { appLayoutMap } from './const';
import App from '../App';
import MicroPanel from '@/component/Popup/panel/micro';
import { getMicroAppInfo, IMicroAppInfo } from '@/microApp';
import styles from './app.less';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const R3fDemo = ({
  item,
  onClick,
}: IProps) => {
  const microAppKey = 'app_r3f';
  const microAppInfo: IMicroAppInfo | null = getMicroAppInfo(microAppKey);

  microAppInfo.props = { showType: 'last' };

  return (
    <App item={item}>
      <div className={styles.container}>
        <MicroPanel microAppInfo={microAppInfo} id="micro-app-container-r3f" />
      </div>
    </App>
  );
};

export default R3fDemo;
